import { db } from './firebase';
import {
  collection,
  getDocs,
  addDoc,
  updateDoc,
  deleteDoc,
  doc,
  setDoc,
  getDoc,
  DocumentData,
  QueryDocumentSnapshot,
} from 'firebase/firestore';

export type FirestoreExam = {
  id?: string;
  mainExam: string;
  examCode: string;
  subExams: string[];
  conductedBy: string;
  examSector: string;
  status: string;
  version: number;
  effectiveDate: any;
  dataSourceLink: string;
  internNote: string;
  filledBy: string;
  reviewStatus: string;
  applicationPeriodStart: any;
  applicationPeriodEnd: any;
  eligibilityCriteria: any[];
};

export type FirestoreUser = {
  id?: string;
  email: string;
  role: 'admin' | 'intern' | 'guest';
  assignedExams: string[];
};

export async function fetchExams(): Promise<FirestoreExam[]> {
  const examsCol = collection(db, 'exams');
  const examSnapshot = await getDocs(examsCol);
  return examSnapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() })) as FirestoreExam[];
}

export async function addExam(exam: FirestoreExam) {
  const examsCol = collection(db, 'exams');
  return await addDoc(examsCol, exam);
}

export async function updateExam(id: string, exam: Partial<FirestoreExam>) {
  const examRef = doc(db, 'exams', id);
  return await updateDoc(examRef, exam);
}

export async function fetchUsers(): Promise<FirestoreUser[]> {
  const usersCol = collection(db, 'users');
  const userSnapshot = await getDocs(usersCol);
  return userSnapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() })) as FirestoreUser[];
}

export async function addUser(user: FirestoreUser) {
  const usersCol = collection(db, 'users');
  return await addDoc(usersCol, user);
}

export async function updateUser(id: string, user: Partial<FirestoreUser>) {
  const userRef = doc(db, 'users', id);
  return await updateDoc(userRef, user);
}

export async function deleteUser(id: string) {
  const userRef = doc(db, 'users', id);
  return await deleteDoc(userRef);
}
