"use client";

import { useState } from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Exam, User } from '@/types';
import {
  fetchExams,
  addExam,
  updateExam,
  fetchUsers,
  addUser,
  updateUser,
  deleteUser,
  FirestoreExam,
  FirestoreUser
} from '@/lib/firestore-utils';
import { useEffect } from 'react';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useToast } from "@/hooks/use-toast";
import { Edit, Trash } from 'lucide-react';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import ExamForm from '@/components/exam-form';
import { format } from 'date-fns';
import { Calendar } from "@/components/ui/calendar";
import { Popover, PopoverTrigger, PopoverContent } from "@/components/ui/popover"
import { useAuth } from "@/components/auth/auth-provider";

// Remove DUMMY_EXAMS. Exams will be fetched from Firestore.

// Remove DUMMY_USERS. Users will be fetched from Firestore.

// Reusable component for displaying exam data in a table
function ExamTable({ exams, handleApproveReject, handleViewExam }: {
  exams: Exam[];
  handleApproveReject: (examId: string, status: 'Approved' | 'Rejected') => void;
  handleViewExam: (exam: Exam) => void;
}) {

  return (
    <div className="overflow-x-auto">
      <Table className="min-w-full">
        <TableHeader>
          <TableRow>
            <TableHead className="w-[200px]">Exam Name</TableHead>
            <TableHead className="w-[150px]">Conducting Body</TableHead>
            <TableHead className="w-[150px]">Exam Level</TableHead>
            <TableHead className="w-[150px]">Category</TableHead>
            <TableHead className="w-[150px]">Filled By</TableHead>
            <TableHead className="w-[150px]">Review Status</TableHead>
            <TableHead className="w-[200px] text-right">Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {exams.map((exam) => (
            <TableRow key={exam.id}>
              <TableCell className="font-medium">{exam.mainExam}</TableCell>
              <TableCell>{exam.conductedBy}</TableCell>
              <TableCell>{exam.examSector}</TableCell>
              <TableCell>{exam.examSector}</TableCell>
              <TableCell>{exam.filledBy || 'N/A'}</TableCell>
              <TableCell>{exam.reviewStatus}</TableCell>
              <TableCell className="text-right">
                {exam.reviewStatus === 'Pending' && (
                  <div className="space-x-2">
                    <Button variant="ghost" size="sm" onClick={() => handleApproveReject(exam.id, 'Approved')}>
                      Approve
                    </Button>
                    <Button variant="destructive" size="sm" onClick={() => handleApproveReject(exam.id, 'Rejected')}>
                      Reject
                    </Button>
                  </div>
                )}
                <Button variant="outline" size="sm" onClick={() => handleViewExam(exam)}>
                  <Edit className="mr-2 h-4 w-4" />
                  View/Edit
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}

type LogEntry = {
  id: string;
  timestamp: Date;
  action: string;
  details: string;
};

function LogTable({ logs }: { logs: LogEntry[] }) {
  return (
    <div className="overflow-x-auto">
      <Table className="min-w-full">
        <TableHeader>
          <TableRow>
            <TableHead>Timestamp</TableHead>
            <TableHead>Action</TableHead>
            <TableHead>Details</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {logs.map((log) => (
            <TableRow key={log.id}>
              <TableCell>{format(log.timestamp, 'PPP p')}</TableCell>
              <TableCell>{log.action}</TableCell>
              <TableCell>{log.details}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}


export default function AdminDashboard() {
  const [exams, setExams] = useState<FirestoreExam[]>([]);
  const [users, setUsers] = useState<FirestoreUser[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    setLoading(true);
    Promise.all([fetchExams(), fetchUsers()])
      .then(([examsData, usersData]) => {
        setExams(examsData);
        setUsers(usersData);
        setLoading(false);
      })
      .catch((err) => {
        setError('Failed to fetch data from Firestore.');
        setLoading(false);
      });
  }, []);
  const [newEmail, setNewEmail] = useState('');
  const [newRole, setNewRole] = useState<'admin' | 'intern' | 'guest'>('intern');
  const { toast } = useToast();
  const [selectedExam, setSelectedExam] = useState<Exam | null>(null);
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false);
  const [assignmentDueDate, setAssignmentDueDate] = useState<Date | null>(null);
  const [selectedExamsForAssignment, setSelectedExamsForAssignment] = useState<string[]>([]);
  const [selectedInternForAssignment, setSelectedInternForAssignment] = useState<string | null>(null);
   const [logs, setLogs] = useState<LogEntry[]>([]);
  const { user } = useAuth();

  const addLog = (action: string, details: string) => {
    const newLog: LogEntry = {
      id: `log-${logs.length + 1}`,
      timestamp: new Date(),
      action: action,
      details: details,
    };
    setLogs((prevLogs) => [newLog, ...prevLogs]);
  };

  const handleApproveReject = async (examId: string, status: 'Approved' | 'Rejected') => {
    try {
      await updateExam(examId, { reviewStatus: status });
      setExams(exams.map(exam => exam.id === examId ? { ...exam, reviewStatus: status } : exam));
    } catch (err) {
      setError('Failed to update exam status.');
    }
  };

  const handleAddUser = async () => {
    if (newEmail && !users.find(user => user.email === newEmail)) {
      const newUser: FirestoreUser = {
        email: newEmail,
        role: newRole,
        assignedExams: [],
      };
      try {
        const docRef = await addUser(newUser);
        setUsers([...users, { ...newUser, id: docRef.id }]);
        setNewEmail('');
        toast({
          title: "User added successfully!",
          description: `User ${newEmail} added as ${newRole}.`,
        });
        addLog('User Added', `Admin ${user?.email} added user ${newEmail} as ${newRole}`);
      } catch (err) {
        setError('Failed to add user.');
      }
    } else {
      alert('Please enter a valid unique email address.');
    }
  };

  const handleRemoveUser = async (userId: string) => {
    const removedUser = users.find(user => user.id === userId);
    try {
      await deleteUser(userId);
      setUsers(users.filter(user => user.id !== userId));
      toast({
        title: "User removed successfully!",
        description: `User removed.`,
      });
      if (removedUser) {
        addLog('User Removed', `Admin ${user?.email} removed user ${removedUser.email} (${removedUser.role})`);
      }
    } catch (err) {
      setError('Failed to remove user.');
    }
  };

  const handleChangeRole = async (userId: string, newRole: 'admin' | 'intern' | 'guest') => {
    const updatedUser = users.find(user => user.id === userId);
    try {
      await updateUser(userId, { role: newRole });
      setUsers(users.map(user => user.id === userId ? { ...user, role: newRole } : user));
      toast({
        title: "User role changed successfully!",
        description: `User role changed to ${newRole}.`,
      });
      if (updatedUser) {
        addLog('User Role Changed', `Admin ${user?.email} changed user ${updatedUser.email} role to ${newRole}`);
      }
    } catch (err) {
      setError('Failed to update user role.');
    }
  };

  const handleViewExam = (exam: Exam) => {
    setSelectedExam(exam);
    setIsEditDialogOpen(true);
  };

  const handleCloseEditDialog = () => {
    setIsEditDialogOpen(false);
    setSelectedExam(null);
  };

   const toggleExamSelection = (examId: string) => {
        setSelectedExamsForAssignment((prevSelected) =>
            prevSelected.includes(examId)
                ? prevSelected.filter((id) => id !== examId)
                : [...prevSelected, examId]
        );
    };

    const handleAssignExamsWithDueDate = () => {
        if (!selectedInternForAssignment) {
            alert('Please select an intern to assign the exams to.');
            return;
        }

        if (selectedExamsForAssignment.length === 0) {
            alert('Please select at least one exam to assign.');
            return;
        }

        if (!assignmentDueDate) {
            alert('Please select a due date for the assignment.');
            return;
        }

        // Update the assignedExams array for the selected intern
        setUsers(users.map(user => {
            if (user.id === selectedInternForAssignment) {
                const assignedExams = [...user.assignedExams, ...selectedExamsForAssignment];
                addLog('Exams Assigned', `Admin ${user?.email} assigned exams ${selectedExamsForAssignment.join(', ')} to intern ${user.email} with due date ${format(assignmentDueDate, 'PPP')}`);
                return { ...user, assignedExams };
            }
            return user;
        }));

        // Clear the selected exams and intern for the next assignment
        setSelectedExamsForAssignment([]);
        setSelectedInternForAssignment(null);
        setAssignmentDueDate(null);
         toast({
            title: "Exams assigned successfully!",
            description: `Exams assigned to intern with due date.`,
        });
    };

  if (loading) return <div className="p-8 text-center">Loading data from Firestore...</div>;
  if (error) return <div className="p-8 text-center text-red-500">{error}</div>;

  return (
    <div className="container mx-auto py-10">
      <Card>
        <CardHeader>
          <CardTitle>Admin Dashboard</CardTitle>
          <CardDescription>Manage exams and intern assignments.</CardDescription>
        </CardHeader>
        <CardContent>
          <ExamTable
            exams={exams}
            handleApproveReject={handleApproveReject}
            handleViewExam={handleViewExam}
          />
        </CardContent>
      </Card>

      <Card className="mt-4">
        <CardHeader>
          <CardTitle>Exam Assignment</CardTitle>
          <CardDescription>Assign exams to interns with a due date.</CardDescription>
        </CardHeader>
        <CardContent>
          {/* Exam Table */}
          <div className="overflow-x-auto">
            <Table className="min-w-full">
              <TableHeader>
                <TableRow>
                  <TableHead className="w-[50px]">Select</TableHead>
                  <TableHead className="w-[200px]">Exam Name</TableHead>
                  <TableHead className="w-[150px]">Conducting Body</TableHead>
                  <TableHead className="w-[150px]">Exam Level</TableHead>
                  <TableHead className="w-[150px]">Category</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {exams.map((exam) => (
                  <TableRow key={exam.id}>
                    <TableCell>
                      <Input
                        type="checkbox"
                        className="h-4 w-4"
                        checked={selectedExamsForAssignment.includes(exam.id)}
                        onChange={() => toggleExamSelection(exam.id)}
                      />
                    </TableCell>
                    <TableCell className="font-medium">{exam.mainExam}</TableCell>
                    <TableCell>{exam.conductedBy}</TableCell>
                    <TableCell>{exam.examSector}</TableCell>
                    <TableCell>{exam.examSector}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          {/* Assignment Controls */}
          <div className="mt-4 flex items-center space-x-4">
            {/* Intern Selection */}
            <div className="w-1/3">
              <Label htmlFor="intern">Assign to Intern:</Label>
              <Select onValueChange={(value) => setSelectedInternForAssignment(value)} value={selectedInternForAssignment || ''}>
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="Select an intern" />
                </SelectTrigger>
                <SelectContent>
                  {users
                    .filter((user) => user.role === 'intern')
                    .map((user) => (
                      <SelectItem key={user.id} value={user.id}>
                        {user.email}
                      </SelectItem>
                    ))}
                </SelectContent>
              </Select>
            </div>

            {/* Due Date Selection */}
            <div className="w-1/3">
              <Label htmlFor="dueDate">Due Date:</Label>
              <Popover>
                <PopoverTrigger asChild>
                  <Button
                    variant={"outline"}
                    className={format(assignmentDueDate || new Date(), "PPP")}
                  >
                    {assignmentDueDate ? (
                      format(assignmentDueDate, "PPP")
                    ) : (
                      <span>Pick a date</span>
                    )}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={assignmentDueDate}
                    onSelect={setAssignmentDueDate}
                    disabled={(date) =>
                      date < new Date()
                    }
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>

            {/* Assign Button */}
            <Button onClick={handleAssignExamsWithDueDate} disabled={selectedExamsForAssignment.length === 0 || !selectedInternForAssignment || !assignmentDueDate}>
              Assign Exams with Due Date
            </Button>
          </div>
        </CardContent>
      </Card>

      <Card className="mt-4">
        <CardHeader>
          <CardTitle>User Management</CardTitle>
          <CardDescription>Add and manage users.</CardDescription>
        </CardHeader>
        <CardContent>
          {/* Add User Form */}
          <div className="grid gap-4 md:grid-cols-3">
            <div>
              <Label htmlFor="email">Email</Label>
              <Input
                type="email"
                id="email"
                placeholder="user@example.com"
                value={newEmail}
                onChange={(e) => setNewEmail(e.target.value)}
              />
            </div>
            <div>
              <Label htmlFor="role">Role</Label>
              <Select onValueChange={(value) => setNewRole(value as 'admin' | 'intern' | 'guest')}>
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="Select a role" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="admin">Admin</SelectItem>
                  <SelectItem value="intern">Intern</SelectItem>
                  <SelectItem value="guest">Guest</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <Button onClick={handleAddUser}>Add User</Button>
          </div>

          {/* User Table */}
          <div className="mt-4 overflow-x-auto">
            <Table className="min-w-full">
              <TableHeader>
                <TableRow>
                  <TableHead>Email</TableHead>
                  <TableHead>Role</TableHead>
                  <TableHead className="text-right">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {users.map((user) => (
                  <TableRow key={user.id}>
                    <TableCell>{user.email}</TableCell>
                    <TableCell>{user.role}</TableCell>
                    <TableCell className="text-right">
                      <div className="space-x-2">
                        <Select onValueChange={(value) => handleChangeRole(user.id, value as 'admin' | 'intern' | 'guest')} defaultValue={user.role}>
                          <SelectTrigger className="w-[180px]">
                            <SelectValue placeholder="Change Role" />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="admin">Admin</SelectItem>
                            <SelectItem value="intern">Intern</SelectItem>
                            <SelectItem value="guest">Guest</SelectItem>
                          </SelectContent>
                        </Select>
                        <Button variant="destructive" size="sm" onClick={() => handleRemoveUser(user.id)}>
                          <Trash className="mr-2 h-4 w-4" />
                          Remove
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>

      <Card className="mt-4">
        <CardHeader>
          <CardTitle>Intern Workload</CardTitle>
          <CardDescription>List of assigned works.</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <Table className="min-w-full">
              <TableHeader>
                <TableRow>
                  <TableHead>Intern Name</TableHead>
                  <TableHead>Assigned Exam</TableHead>
                  <TableHead>Due Date</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {users
                  .filter((user) => user.role === 'intern')
                  .map((user) => {
                    return user.assignedExams.map((examId) => {
                      const exam = exams.find((e) => e.id === examId);
                      if (!exam) return null;

                      return (
                        <TableRow key={`${user.id}-${exam.id}`}>
                          <TableCell>{user.email}</TableCell>
                          <TableCell>{exam.mainExam}</TableCell>
                          <TableCell>{format(new Date(), "PPP")}</TableCell>
                          <TableCell>Pending</TableCell>
                        </TableRow>
                      );
                    });
                  }).filter(Boolean)}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>

      <Card className="mt-4">
        <CardHeader>
          <CardTitle>Log Details</CardTitle>
          <CardDescription>Audit trail of activities.</CardDescription>
        </CardHeader>
        <CardContent>
          <LogTable logs={logs} />
        </CardContent>
      </Card>

      <Dialog open={isEditDialogOpen} onOpenChange={setIsEditDialogOpen}>
        <DialogContent className="sm:max-w-[80%]">
          <DialogHeader>
            <DialogTitle>View/Edit Exam</DialogTitle>
            <DialogDescription>
              View or edit the details of the selected exam.
            </DialogDescription>
          </DialogHeader>
          {selectedExam && (
            <ExamForm exam={selectedExam} onClose={handleCloseEditDialog} />
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
}

